
<template>
  <!--credit:  https://dev.to/ayushmanbthakur/how-to-make-tic-tac-toe-in-browser-with-html-css-and-js-28ed-->
  <div class="container" :class="{ 'dark-mode': modestyle }">
    <h1>Tic-Tac-Toe</h1>
    <div class="info">
      <div class="input">
        <input type="checkbox" id="switch" @click="switchMode(mode)" />
        <label for="switch">Toggle</label>
        <span>{{ mode }} Mode</span>
      </div>
    </div>
    <div class="play-area">
      <div id="block_0" class="block" @click="draw(0)">{{ content[0] }}</div>
      <div id="block_1" class="block" @click="draw(1)">{{ content[1] }}</div>
      <div id="block_2" class="block" @click="draw(2)">{{ content[2] }}</div>
      <div id="block_3" class="block" @click="draw(3)">{{ content[3] }}</div>
      <div id="block_4" class="block" @click="draw(4)">{{ content[4] }}</div>
      <div id="block_5" class="block" @click="draw(5)">{{ content[5] }}</div>
      <div id="block_6" class="block" @click="draw(6)">{{ content[6] }}</div>
      <div id="block_7" class="block" @click="draw(7)">{{ content[7] }}</div>
      <div id="block_8" class="block" @click="draw(8)">{{ content[8] }}</div>
    </div>
    <!--    Reset board-->
    <button @click="resetBoard()" v-if="isOver || isTie">RESET BOARD</button>
    <h2 id="winner" v-if="isOver"> Winner is {{ winner }} </h2>
    <h2 v-if="isTie"> Game is Tie</h2>
  </div>
</template>

<script>
export default {
  name: 'TictactoeView',
  components: {
  },
  data() {
    return {
      mode: [],
      modestyle: false,
      darkTheme: false,
      content: ['', '', '', '', '', '', '', '', '',],
      turn: true,
      isOver: false,
      winner: null,
      isTie: false,
    }
  },
  mounted() {
    this.mode = "Light";
  },
  methods: {
    switchMode(mode) {
      if (mode == "Light") {
        this.mode = 'Dark'
        this.modestyle = true
      } else {
        this.mode = 'Light'
        this.modestyle = false
      }
    },
    draw(index) {
      // if turn is true then mark as X or mark as O
      if (this.content[index] == '' && this.isOver == false) {
        this.content.splice(index, '');
        var element = document.getElementById("block_" + index);
        if (this.turn) {
          this.content[index] = 'X';
          element.classList.add("fluo-r");
        } else {
          this.content[index] = 'O';
          element.classList.add("fluo-g");
        }
        this.turn = !this.turn;
      }
      this.calculateWinner();
      this.calculateTie();
    },
    // if the same row, column or diagonals are occupied by the same player, he/she wins.
    calculateWinner() {
      const WIN_CONDITIONS = [
        // rows
        [0, 1, 2], [3, 4, 5], [6, 7, 8],
        // cols
        [0, 3, 6], [1, 4, 7], [2, 5, 8],
        // diagonals
        [0, 4, 8], [2, 4, 6]
      ];
      for (let i = 0; i < WIN_CONDITIONS.length; i++) {
        let firstIndex = WIN_CONDITIONS[i][0];
        let secondIndex = WIN_CONDITIONS[i][1];
        let thirdIndex = WIN_CONDITIONS[i][2];
        if (this.content[firstIndex] == this.content[secondIndex] &&
          this.content[firstIndex] == this.content[thirdIndex] &&
          this.content[firstIndex] != "") {
          this.isOver = true;
          this.winner = this.content[firstIndex];
        }
      }
    },
    // if there is an empty block, the game is not tied.
    calculateTie() {
      if (!this.winner) {
        for (let i = 0; i <= 8; i++) {
          if (this.content[i] == "") {
            return
          }
        }
        this.isTie = true;
      }
    },
    resetBoard() {
      for (let i = 0; i <= 8; i++) {
        var element = document.getElementById("block_" + i);
        element.classList.remove("fluo-g");
        element.classList.remove("fluo-r");
        this.content[i] = ""
        this.isOver = false;
        this.winner = null;
        this.isTie = false;
      }
    }
  }
}
</script>

<style lang="scss" scoped>
.container {
  min-height: 100vh;
  width: 100vw;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background: rgb(255, 255, 255);

  @media all and (max-width: 992px) {
    justify-content: flex-start;
    padding-top: 20px;
  }

  &.dark-mode {
    background: rgba(0, 0, 0, 0.977);

    h1 {
      color: rgb(255, 0, 234);
      text-shadow:
        0 0 2px #fff,
        0 0 52px #f09,
    }

    h2 {
      color: #fefefe;
    }

    span {
      color: #fefefe;
    }

    .play-area {
      .block {
        border-color: #fefefe;
        transition: all 0.3s ease-in-out;

        &.fluo-g {
          background-color: transparent;
          color: #2bf342;
          text-shadow:
            0 0 2px #fff,
            0 0 52px rgb(34, 255, 0),
        }

        &.fluo-r {
          background-color: transparent;
          color: #f13636;
          text-shadow:
            0 0 2px #fff,
            0 0 52px rgb(255, 0, 0),
        }
      }
    }

    button {
      color: rgb(0, 0, 0);
      box-shadow:
        0 0 2px #fff,
        0 0 10px rgb(208, 230, 11),
    }
  }

  h1 {
    color: rgb(0, 0, 0);
    font-size: 5rem;

    @media only screen and (max-width: 600px) {
      font-size: 3rem;
    }
  }

  h2 {
    color: rgb(0, 0, 0);
    margin-top: 0.5em;
    font-size: 2rem;
    margin-bottom: 0.5em;

    @media only screen and (max-width: 600px) {
      font-size: 1.3rem;
    }
  }

  .info {
    display: flex;
    justify-content: center;
    align-items: center;

    .input {
      text-align: center;

      input[type=checkbox] {
        height: 0;
        width: 0;
        visibility: hidden;
      }

      label {
        cursor: pointer;
        text-indent: -9999px;
        height: 40px;
        background: grey;
        display: block;
        border-radius: 100px;
        position: relative;

        &::after {
          content: '';
          position: absolute;
          top: 5px;
          left: 5px;
          width: 30px;
          height: 30px;
          background: rgba(7, 7, 7, 0.51);
          border-radius: 90px;
          transition: 0.3s;
        }

        &::active {
          &::after {
            width: 130px;
          }
        }
      }

      input:checked+label {
        background: #ffffff;

        &::after {
          left: calc(100% - 5px);
          transform: translateX(-100%);
        }
      }
    }

  }

  .play-area {
    display: grid;
    width: 300px;
    height: 300px;
    grid-template-columns: auto auto auto;
    margin-top: 0.5em;

    .block {
      display: flex;
      width: 100px;
      height: 100px;
      align-items: center;
      justify-content: center;
      font-size: 3rem;
      font-weight: bold;
      border: 3px solid black;
      transition: all 0.3s ease-in-out;

      &.fluo-g {
        background-color: #1DB954;
        color: #fefefe;
      }

      &.fluo-r {
        background-color: #fb4747;
        color: #fefefe;
      }
    }

    .block:hover {
      cursor: pointer;
    }

    #block_0,
    #block_1,
    #block_2 {
      border-top: none;
    }

    #block_0,
    #block_3,
    #block_6 {
      border-left: none;
    }

    #block_6,
    #block_7,
    #block_8 {
      border-bottom: none;
    }

    #block_2,
    #block_5,
    #block_8 {
      border-right: none;
    }
  }
}

button {
  margin-top: 0.5em;
  background-color: #ffff00;
  padding: 7px;
  width: 150px;
  animation: btnbounce 4s infinite;
  cursor: pointer;
}

@keyframes btnbounce {

  5%,
  50% {
    transform: scale(1);
  }

  10% {
    transform: scale(1);
  }

  15% {
    transform: scale(1);
  }

  20% {
    transform: scale(1) rotate(-5deg);
  }

  25% {
    transform: scale(1) rotate(5deg);
  }

  30% {
    transform: scale(1) rotate(-3deg);
  }

  35% {
    transform: scale(1) rotate(2deg);
  }

  40% {
    transform: scale(1) rotate(0);
  }
}
</style>